* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	width: 100%;
	background-color: #f5f5f5;
}
.live-search {
	max-width: 100%;
	background-color: #fff;
	padding: 20px;
	border-radius: 10px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	display: flex;
	justify-content: center;
	align-items: center;
}
.live-search input[type="text"] {
	width: 60%;
	padding: 10px;
	border: none;
	outline: none;
	font-size: 16px;
	border-radius: 10px;
	border: 1px solid #ccc;
	transition: all 0.3s ease-in-out;
}
.icons-container {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 50px;
}

.icon-box {
	width: 100px;
	height: 100px;
	background-color: #fff;
	margin: 10px;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	transition: all 0.3s ease-in-out;
}

.icon {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	transition: all 0.3s ease-in-out;
	padding: 10px;
}
.icon div {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.icon p {
	flex: 1;
	margin-top: 10px;
	font-size: 12px;
	font-weight: 600;
	color: #333;
    width: 84px;
    overflow: hidden;
    white-space: nowrap;
}
